"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import DashboardHeader from "@/components/dashboard/dashboard-header"
import DashboardSidebar from "@/components/dashboard/dashboard-sidebar"
import BlogManager from "@/components/dashboard/blog-manager"
import CategoryManager from "@/components/dashboard/category-manager"
import LinkManager from "@/components/dashboard/link-manager"
import ColorManager from "@/components/dashboard/color-manager"
import { useToast } from "@/hooks/use-toast"

interface BlogStats {
  totalBlogs: number
  totalCategories: number
  totalLinks: number
}

export default function DashboardPage() {
  const [activeTab, setActiveTab] = useState("dashboard")
  const [stats, setStats] = useState<BlogStats>({ totalBlogs: 0, totalCategories: 0, totalLinks: 0 })
  const [loading, setLoading] = useState(true)
  const router = useRouter()
  const { toast } = useToast()

  useEffect(() => {
    const loadStats = async () => {
      try {
        const [blogsRes, categoriesRes, linksRes] = await Promise.all([
          fetch("/api/blogs"),
          fetch("/api/categories"),
          fetch("/api/links"),
        ])

        const blogs = await blogsRes.json()
        const categories = await categoriesRes.json()
        const links = await linksRes.json()

        setStats({
          totalBlogs: Array.isArray(blogs) ? blogs.filter((b: any) => b.status === 1).length : 0,
          totalCategories: Array.isArray(categories) ? categories.length : 0,
          totalLinks: Array.isArray(links) ? links.length : 0,
        })
      } catch (error) {
        console.error("Failed to load stats:", error)
      } finally {
        setLoading(false)
      }
    }

    loadStats()
  }, [])

  const handleLogout = async () => {
    try {
      await fetch("/api/auth/logout", { method: "POST" })
      toast({ title: "Success", description: "Logged out successfully" })
      router.push("/dashboard/login")
    } catch (error) {
      toast({ title: "Error", description: "Logout failed", variant: "destructive" })
    }
  }

  return (
    <div className="min-h-screen bg-gray-200 flex flex-col">
      <DashboardHeader userName="John Doe" userRole="Administrator" />

      <div className="flex flex-1 overflow-hidden">
        <DashboardSidebar activeTab={activeTab} setActiveTab={setActiveTab} onLogout={handleLogout} />

        {/* Main Content */}
        <div className="flex-1 p-6 overflow-auto">
          {activeTab === "dashboard" && (
            <div className="space-y-6">
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                <div className="bg-white rounded-md border border-gray-100 p-6 shadow-md shadow-black/5">
                  <div className="flex justify-between mb-6">
                    <div>
                      <div className="flex items-center mb-1">
                        <div className="text-2xl font-semibold">{stats.totalBlogs}</div>
                      </div>
                      <div className="text-sm font-medium text-gray-400">Total Blogs</div>
                    </div>
                    <span className="text-2xl">ðŸ“</span>
                  </div>
                  <button onClick={() => setActiveTab("blogs")} className="text-[#f84525] font-medium text-sm hover:text-red-800">
                    View
                  </button>
                </div>

                <div className="bg-white rounded-md border border-gray-100 p-6 shadow-md shadow-black/5">
                  <div className="flex justify-between mb-6">
                    <div>
                      <div className="flex items-center mb-1">
                        <div className="text-2xl font-semibold">{stats.totalCategories}</div>
                      </div>
                      <div className="text-sm font-medium text-gray-400">Categories</div>
                    </div>
                    <span className="text-2xl">ðŸ“‚</span>
                  </div>
                  <button onClick={() => setActiveTab("categories")} className="text-[#f84525] font-medium text-sm hover:text-red-800">
                    View
                  </button>
                </div>

                <div className="bg-white rounded-md border border-gray-100 p-6 shadow-md shadow-black/5">
                  <div className="flex justify-between mb-6">
                    <div>
                      <div className="flex items-center mb-1">
                        <div className="text-2xl font-semibold">{stats.totalLinks}</div>
                      </div>
                      <div className="text-sm font-medium text-gray-400">Links</div>
                    </div>
                    <span className="text-2xl">ðŸ”—</span>
                  </div>
                  <button onClick={() => setActiveTab("links")} className="text-[#f84525] font-medium text-sm hover:text-red-800">
                    View
                  </button>
                </div>
              </div>

              {/* Activities section */}
              <div className="bg-white border border-gray-100 shadow-md shadow-black/5 p-6 rounded-md">
                <div className="flex justify-between mb-4 items-start">
                  <div className="font-medium">Recent Activity</div>
                </div>
                <div className="overflow-hidden">
                  <table className="w-full min-w-[540px]">
                    <tbody>
                      <tr>
                        <td className="py-2 px-4 border-b border-b-gray-50">
                          <div className="flex items-center">
                            <a href="#" className="text-gray-600 text-sm font-medium hover:text-blue-500 ml-2 truncate">
                              Blog created or updated
                            </a>
                          </div>
                        </td>
                        <td className="py-2 px-4 border-b border-b-gray-50">
                          <span className="text-[13px] font-medium text-gray-400">Just now</span>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          )}

          {activeTab === "blogs" && <BlogManager />}
          {activeTab === "categories" && <CategoryManager />}
          {activeTab === "links" && <LinkManager />}
          {activeTab === "colors" && <ColorManager />}
        </div>
      </div>
    </div>
  )
}
