"use client"

import { useEffect, useState } from "react"
import { Button } from "@/components/ui/button"
import { useToast } from "@/hooks/use-toast"
import BlogForm from "./blog-form"
import BlogList from "./blog-list"

export default function BlogManager() {
  const [blogs, setBlogs] = useState([])
  const [showForm, setShowForm] = useState(false)
  const [editingBlog, setEditingBlog] = useState(null)
  const [loading, setLoading] = useState(true)
  const { toast } = useToast()

  useEffect(() => {
    fetchBlogs()
  }, [])

  const fetchBlogs = async () => {
    try {
      const response = await fetch("/api/admin/blogs")
      const data = await response.json()
      setBlogs(data)
    } catch (error) {
      toast({ title: "Error", description: "Failed to fetch blogs", variant: "destructive" })
    } finally {
      setLoading(false)
    }
  }

  const handleDelete = async (id: string) => {
    if (!confirm("Are you sure you want to delete this blog?")) return

    try {
      const response = await fetch(`/api/admin/blogs/${id}`, { method: "DELETE" })
      if (response.ok) {
        setBlogs(blogs.filter((b) => b.id !== id))
        toast({ title: "Success", description: "Blog deleted" })
      }
    } catch (error) {
      toast({ title: "Error", description: "Failed to delete blog", variant: "destructive" })
    }
  }

  const handleSave = async (blog: any) => {
    try {
      if (editingBlog) {
        const response = await fetch(`/api/admin/blogs/${blog.id}`, {
          method: "PUT",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(blog),
        })
        if (response.ok) {
          const updated = await response.json()
          setBlogs(blogs.map((b) => (b.id === updated.id ? updated : b)))
          toast({ title: "Success", description: "Blog updated" })
        }
      } else {
        const response = await fetch("/api/admin/blogs", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(blog),
        })
        if (response.ok) {
          const newBlog = await response.json()
          setBlogs([...blogs, newBlog])
          toast({ title: "Success", description: "Blog created" })
        }
      }
      setShowForm(false)
      setEditingBlog(null)
    } catch (error) {
      toast({ title: "Error", description: "Failed to save blog", variant: "destructive" })
    }
  }

  if (loading) return <div>Loading...</div>

  return (
    <div>
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-3xl font-bold">Blog Posts</h2>
        <Button
          onClick={() => {
            setShowForm(true)
            setEditingBlog(null)
          }}
        >
          Add New Blog
        </Button>
      </div>

      {showForm && (
        <BlogForm
          blog={editingBlog}
          onSave={handleSave}
          onCancel={() => {
            setShowForm(false)
            setEditingBlog(null)
          }}
        />
      )}

      <BlogList
        blogs={blogs}
        onEdit={(blog) => {
          setEditingBlog(blog)
          setShowForm(true)
        }}
        onDelete={handleDelete}
      />
    </div>
  )
}
