"use client"

import type React from "react"

import { useEffect, useState } from "react"
import { Button } from "@/components/ui/button"
import { useToast } from "@/hooks/use-toast"

export default function BlogForm({ blog, onSave, onCancel }: any) {
  const [formData, setFormData] = useState({
    title: "",
    excerpt: "",
    contentHtml: "",
    author: "",
    status: 1,
    featured: 0,
    categoryIds: [],
    images: [],
    video: "",
  })

  const [categories, setCategories] = useState([])
  const { toast } = useToast()

  useEffect(() => {
    fetchCategories()
    if (blog) {
      setFormData(blog)
    }
  }, [blog])

  const fetchCategories = async () => {
    try {
      const response = await fetch("/api/categories")
      const data = await response.json()
      setCategories(data)
    } catch (error) {
      console.error("Failed to fetch categories:", error)
    }
  }

  const handleInputChange = (e: any) => {
    const { name, value, type, checked } = e.target
    setFormData({
      ...formData,
      [name]: type === "checkbox" ? (checked ? 1 : 0) : value,
    })
  }

  const handleCategoryToggle = (catId: string) => {
    setFormData({
      ...formData,
      categoryIds: formData.categoryIds.includes(catId)
        ? formData.categoryIds.filter((id: string) => id !== catId)
        : [...formData.categoryIds, catId],
    })
  }

  const handleFileUpload = async (e: any) => {
    const files = e.target.files
    for (const file of files) {
      const formDataFile = new FormData()
      formDataFile.append("file", file)
      try {
        const response = await fetch("/api/upload", {
          method: "POST",
          body: formDataFile,
        })
        const data = await response.json()
        setFormData({
          ...formData,
          images: [...formData.images, data.url],
        })
        toast({ title: "Success", description: "Image uploaded" })
      } catch (error) {
        toast({ title: "Error", description: "Upload failed", variant: "destructive" })
      }
    }
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    onSave(formData)
  }

  return (
    <div className="bg-white rounded-lg shadow p-6 mb-6">
      <h3 className="text-xl font-bold mb-4">{blog ? "Edit Blog" : "Create New Blog"}</h3>
      <form onSubmit={handleSubmit} className="space-y-6">
        <div className="grid grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-medium mb-2">Title</label>
            <input
              type="text"
              name="title"
              value={formData.title}
              onChange={handleInputChange}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:border-blue-500"
              required
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-2">Author</label>
            <input
              type="text"
              name="author"
              value={formData.author}
              onChange={handleInputChange}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:border-blue-500"
            />
          </div>
        </div>

        <div>
          <label className="block text-sm font-medium mb-2">Excerpt</label>
          <textarea
            name="excerpt"
            value={formData.excerpt}
            onChange={handleInputChange}
            className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:border-blue-500 min-h-20 resize-none"
          />
        </div>

        <div>
          <label className="block text-sm font-medium mb-2">Content (HTML)</label>
          <textarea
            name="contentHtml"
            value={formData.contentHtml}
            onChange={handleInputChange}
            className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:border-blue-500 min-h-32 resize-none font-mono"
          />
        </div>

        <div className="grid grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-medium mb-2">Status</label>
            <select
              name="status"
              value={formData.status}
              onChange={handleInputChange}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:border-blue-500"
            >
              <option value="0">Draft</option>
              <option value="1">Published</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium mb-2">Featured</label>
            <select
              name="featured"
              value={formData.featured}
              onChange={handleInputChange}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:border-blue-500"
            >
              <option value="0">No</option>
              <option value="1">Yes</option>
            </select>
          </div>
        </div>

        <div>
          <label className="block text-sm font-medium mb-2">Categories</label>
          <div className="space-y-2">
            {categories.map((cat: any) => (
              <label key={cat.id} className="flex items-center gap-2 cursor-pointer">
                <input
                  type="checkbox"
                  checked={formData.categoryIds.includes(cat.id)}
                  onChange={() => handleCategoryToggle(cat.id)}
                  className="w-4 h-4 rounded"
                />
                <span>{cat.name}</span>
              </label>
            ))}
          </div>
        </div>

        <div>
          <label className="block text-sm font-medium mb-2">Upload Images</label>
          <input
            type="file"
            multiple
            accept="image/*"
            onChange={handleFileUpload}
            className="w-full px-4 py-2 border border-gray-300 rounded-lg"
          />
          {formData.images.length > 0 && (
            <div className="mt-4 grid grid-cols-4 gap-4">
              {formData.images.map((img: string, idx: number) => (
                <div key={idx} className="relative">
                  <img
                    src={img || "/placeholder.svg"}
                    alt={`Preview ${idx}`}
                    className="rounded w-full h-24 object-cover"
                  />
                  <button
                    type="button"
                    onClick={() =>
                      setFormData({
                        ...formData,
                        images: formData.images.filter((_: any, i: number) => i !== idx),
                      })
                    }
                    className="absolute top-1 right-1 bg-red-500 text-white rounded-full w-6 h-6 flex items-center justify-center text-sm"
                  >
                    Ã—
                  </button>
                </div>
              ))}
            </div>
          )}
        </div>

        <div className="flex gap-4">
          <Button type="submit" className="flex-1">
            {blog ? "Update Blog" : "Create Blog"}
          </Button>
          <Button type="button" variant="outline" onClick={onCancel} className="flex-1 bg-transparent">
            Cancel
          </Button>
        </div>
      </form>
    </div>
  )
}
