"use client"

import { Home, Users, Activity, FileText, Archive, Bell, Mail, LogOut, ChevronRight } from "lucide-react"
import { Button } from "@/components/ui/button"

interface DashboardSidebarProps {
  activeTab: string
  setActiveTab: (tab: string) => void
  onLogout: () => void
}

export default function DashboardSidebar({ activeTab, setActiveTab, onLogout }: DashboardSidebarProps) {
  const menuItems = [
    {
      section: "ADMIN",
      items: [
        { id: "dashboard", label: "Dashboard", icon: Home },
        { id: "users", label: "Users", icon: Users, hasArrow: true },
        { id: "activities", label: "Activities", icon: Activity },
      ],
    },
    {
      section: "BLOG",
      items: [
        { id: "blogs", label: "Post", icon: FileText, hasArrow: true },
        { id: "categories", label: "Categories", icon: Archive },
      ],
    },
    {
      section: "PERSONAL",
      items: [
        { id: "notifications", label: "Notifications", icon: Bell, badge: "5" },
        { id: "links", label: "Links", icon: Mail, badge: "2 New" },
      ],
    },
  ]

  return (
    <div className="w-64 bg-white border-r border-gray-200 p-6 flex flex-col overflow-y-auto">
      {/* Logo */}
      <div className="mb-8">
        <h1 className="text-lg font-bold text-gray-900">
          LOREM <span className="bg-red-500 text-white px-2 py-1 rounded text-sm">IPSUM</span>
        </h1>
      </div>

      {/* Navigation */}
      <nav className="flex-1 space-y-8">
        {menuItems.map((section) => (
          <div key={section.section}>
            <p className="text-xs font-semibold text-gray-400 uppercase mb-3">{section.section}</p>
            <div className="space-y-2">
              {section.items.map((item) => {
                const Icon = item.icon
                return (
                  <button
                    key={item.id}
                    onClick={() => setActiveTab(item.id)}
                    className={`w-full flex items-center justify-between px-4 py-3 rounded-lg transition ${
                      activeTab === item.id ? "bg-gray-100 text-gray-900 font-medium" : "text-gray-700 hover:bg-gray-50"
                    }`}
                  >
                    <div className="flex items-center gap-3">
                      <Icon size={18} />
                      <span className="text-sm">{item.label}</span>
                    </div>
                    <div className="flex items-center gap-2">
                      {item.badge && (
                        <span
                          className={`text-xs px-2 py-1 rounded-full ${
                            item.badge === "5" ? "bg-red-100 text-red-600" : "bg-green-100 text-green-600"
                          }`}
                        >
                          {item.badge}
                        </span>
                      )}
                      {item.hasArrow && <ChevronRight size={16} className="text-gray-400" />}
                    </div>
                  </button>
                )
              })}
            </div>
          </div>
        ))}
      </nav>

      {/* Logout Button */}
      <Button onClick={onLogout} className="w-full bg-gray-900 hover:bg-gray-800 text-white rounded-lg py-2">
        <LogOut size={16} className="mr-2" />
        Logout
      </Button>
    </div>
  )
}
